<template>
  <router-link
    to="/player"
    class="my-8 mx-4 card-item"
    @sn:focused="setActiveMenu(LIVESTREAM_MENU)"
    @sn:unfocused="setActiveMenu(0)"
    v-focus
  >
    <div class="relative">
      <img
        class="rounded-xl box-border"
        :src="item.thumbnail"
      />
      <span class="live-label rounded-full absolute left-2 bottom-2 text-active py-1 px-4 flex items-center justify-center">
        <span class="rounded-full w-2 h-2 bg-active mx-1"></span>
        <span>Live</span>
      </span>
    </div>
    <div class="mx-2">
      <p class="mt-4 text-xl leading-9 font-semibold text-inactive">{{ item.name }}</p>
      <span class="text-inactive leading-8 text-md">{{ formatDate(item.over_time) }}</span>
    </div>
  </router-link>
</template>

<script>
import { LIVESTREAM_MENU } from '~/constants/menu'
import { mapActions } from 'vuex'
export default {
  name: 'CardItem',

  props: {
    item: {
      type: Object,
      default: () => { }
    }
  },
  data: () => ({
    LIVESTREAM_MENU
  }),

  mounted() {
    // this.getLiveEvents()
  },

  methods: {
    ...mapActions({
      setActiveMenu: 'menu/setActiveMenu'
    }),

    async getLiveEvents() {
      const data = await this.$services.livestream.getLiveEvents({
        params: {
          page_num: 1,
          page_size: 10
        }
      })
      console.warn(data)
    },

    formatDate(value) {
      return this.$moment(value).format('hh:mm | DD/MM')
    }
  }
}
</script>

<style scoped>
.live-label {
  background: #fc414a;
}
:focus {
  outline: none;
}
:focus img {
  border: 3px solid #e1e1e1;
}
:focus * {
  color: #e1e1e1;
}
</style>