<template>
  <ul
    id="nav"
    class="flex h-full flex-col justify-center"
    :class="{ 'is-open': activeMenu }"
    v-focus-section
  >
    <li
      v-for="item in menu"
      :key="item.id"
      class="nav-link flex flex-col p-0 static w-full h-32 flex-none order-none flex-grow-0 items-center justify-center"
      :class="{ 'active-menu': activeMenu == item.id }"
      @sn:focused="setActiveMenu(item.id)"
      @sn:unfocused="setActiveMenu(0)"
    >
      <router-link
        :to="item.link"
        class="pb-2"
        v-focus
      >
        <img :src="item.icon">
      </router-link>
    </li>
  </ul>
</template>

<script>
import { MENU } from '~/constants/menu'
import { mapState, mapActions } from 'vuex'

export default {
  computed: {
    ...mapState({
      activeMenu: state => state.menu.activeMenu
    })
  },

  data() {
    return {
      menu: MENU
    }
  },

  methods: {
    ...mapActions({
      setActiveMenu: 'menu/setActiveMenu'
    })
  }
}
</script>

<style scoped>
.nav-link :focus {
  outline: none;
  border-bottom: 8px solid #006dff;
}
.active-menu a {
  border-bottom: 8px solid #006dff;
}
#nav {
  transition: all 0.5s ease;
}
</style>