import axios from 'axios'
import LivestreamService from '~/services/livestream.service'

const bind = ($axios, createService) => createService($axios)

const $axios = axios
$axios.defaults.baseURL = process.env.VUE_APP_API_BASE_URL

$axios.interceptors.request.use((config) => {
  config.headers['x-api-key'] = process.env.VUE_APP_APP_KEY

  return config
})

export default {
  livestream: bind($axios, LivestreamService),
}