import { SET_ACTIVE_MENU } from '~/constants/mutation-types'

const state = () => ({
  activeMenu: 0,
})

const getters = {
  activeMenu: state => state.activeMenu
}

const mutations = {
  [SET_ACTIVE_MENU]: (state, payload) => {
    state.activeMenu = payload
  },
}

const actions = {
  /**
   * Set active menu
   *
   * @param {Function} commit
   * @param {Boolean} payload
   */
  setActiveMenu({ commit }, payload) {
    commit(SET_ACTIVE_MENU, payload)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}