import Vue from 'vue'
import Router from 'vue-router'
import Home from '~/page/Home.vue'
import Livestream from '~/page/Livestream.vue'
import Player from '~/page/Player.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/livestream',
      name: 'Livestream',
      component: Livestream
    },
    {
      path: '/player',
      name: 'Player',
      component: Player
    },
  ]
})